@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';

.wrapper.welcome {
	.header_wrapper {
		border-bottom: none;
	}

	.contents_wrap {
		padding-top: 0;
	}

	.footer_wrapper {
		margin-top: 0;
	}
}

.welcome_wrap {
	.prod_info_box {
		.badge_ebook {
			color: #3471c1;
			border: 1px solid #eaf0f9;
			background: #eaf0f9;
		}

		.badge_sam {
			color: #2badb1;
			background: #e9f7f7;
			border: 1px solid #e9f7f7;
		}

		.badge_hottracks {
			color: #d85954;
			border: 1px solid #fbeeed;
			background: #fbeeed;
		}

		.prod_md_comment {
			position: relative;
			color: variables.$textColorPurple;
			margin-top: 30px;
			padding-left: 20px;
			@include mixins.font_size_xs;
			font-weight: variables.$fontWeightBold;

			&:before {
				content: '';
				position: absolute;
				display: block;
				width: 16px;
				height: 16px;
				@include mixins.ratio_images('../images/ico_md_comment', 'png', 16px, 16px);
				top: 3px;
				left: 0;
			}
		}

		.prod_md_comment_desc {
			margin-top: 12px;
			color: variables.$textColorGray;
			@include mixins.font_size_xs;
			@include mixins.multi_ellipsis(variables.$fontSizeXsLineHeight, 3, false);
			word-break: break-all;
		}
	}
}

/* 핫라인 영역 */
.hot_line_wrap {
	position: relative;

	&:before {
		content: '';
		position: absolute;
		display: inline-block;
		height: 1px;
		background: linear-gradient(90deg, #5055b1 0%, #5eb05d 26%, #78b5ae 58%, #5055b1 81%, #5055b1 100%);
		left: 0;
		right: 0;
		bottom: 0;
	}

	.contents_inner {
		position: relative;

		.hot_line_inner {
			padding-top: 50px;
			display: none;
		}

		.btn_hot_line {
			position: absolute;
			width: 46px;
			height: 16px;
			right: 0;
			bottom: -16px;
			background: url('../../ink/welcome/images/btn_welcome_hot_line.svg') no-repeat center;
			background-size: 46px 16px;
			z-index: 1;

			&:after {
				content: '';
				display: inline-block;
				width: 10px;
				height: 6px;
				margin-top: 4px;
				vertical-align: top;
				background: {
					position: center;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../../ink/welcome/images/arw_welcome_hot_line', 'png', 10px, 6px);
				@include mixins.transition(transform .23s);
			}
		}

	}

	&.active {
		.hot_line_inner {
			display: block;
		}

		.btn_hot_line:after {
			transform: rotate(180deg);
		}
	}
}

/* 웰컴 배너 영역 */
.welcome_banner_wrap {
	position: relative;
	padding-top: 60px;

	.welcome_banner_inner {
		position: relative;
		margin-left: calc((100% - #{variables.$defaultInnerWidth}) / 2 - 64px);

		.swiper-slide {
			position: relative;
			//background: #fff;
			height: 572px;

			.banner_link {
				display: inline-block;
				width: 100%;
				height: 100%;
			}
		}

		.banner_title {
			margin-left: 64px;
			font-size: 38px;
			line-height: 55px;
			letter-spacing: variables.$letterSpacing;
			font-weight: variables.$fontWeightBold;
		}

		.banner_visual_box {
			position: absolute;
			border-radius: 20px 0 0 20px;
			left: 0;
			right: 0;
			bottom: 0;

			.img_box {
				overflow: hidden;
			}

			.banner_badge_box {
				position: absolute;
				left: 64px;
				top: -15px;
			}

			.banner_badge {
				display: inline-block;
				color: #fff;
				background: variables.$textColorLightGray;
				padding: 0 10px;
				border-radius: 6px 0 6px 0;
				font-size: variables.$fontSizeXxs;
				line-height: 30px;
				letter-spacing: variables.$letterSpacing;
				font-weight: variables.$fontWeightBold;

				&.live {
					background: #ff393b;

					&:before {
						content: '';
						display: inline-block;
						width: 5px;
						height: 5px;
						background: #fff;
						border-radius: 50%;
						margin: 0 4px 3px 0;
						vertical-align: middle;
					}
				}

				&.time:before {
					content: '';
					display: inline-block;
					width: 13px;
					height: 13px;
					margin: 1px 4px 0 0;
					background: {
						repeat: no-repeat;
						position: center;
					}
					vertical-align: text-top;
					@include mixins.ratio_images('../images/ico_onair_clock', 'png', 13px, 13px);
				}
			}
		}

		.banner_floating_box {
			position: absolute;
			top: 0;
			right: calc(100% - #{variables.$defaultInnerWidth} - 64px);
			z-index: 1;

			.banner_control_box, .banner_content_box {
				vertical-align: top;
			}

			.banner_control_box {
				@include mixins.justify_width(center, start, row, true);
				margin-top: 78px;

				.swiper-pagination-fraction {
					color: variables.$textColorLightGray;
					@include mixins.font_size_xs;

					position: relative;
					left: auto;
					bottom: auto;
					transform: none;
					width: auto;
					min-width: auto;
					height: auto;
					padding: 0;
					border-radius: 0;
					background: none;
					box-sizing: border-box;
					font-size: 0;

					.swiper-pagination-current {
						color: variables.$textColorBlack;
						font-weight: variables.$fontWeightMedium;
					}

					.swiper-pagination-total {

					}
				}

				.btn_swiper_pause {
					width: 30px;
					height: 30px;
					margin-left: 12px;
					background: {
						repeat: no-repeat;
						position: 0 0;
					}
					@include mixins.ratio_images('../images/arw_swiper_pause_resume', 'png', 30px, 60px);

					&.paused {
						background-position-y: -30px;
					}
				}
			}

			.banner_content_box {
				display: inline-block;
				width: 273px;
				background: #fff;
				border-radius: 20px 0 20px 20px;
				margin-left: 50px;
				vertical-align: top;

				.content_info_box {
					padding: 20px 24px 30px;

					.content_type {
						color: variables.$textColorPurple;
						@include mixins.font_size_xxs;
						font-weight: variables.$fontWeightMedium;
					}

					.content_title {
						margin-top: 5px;
						@include mixins.font_size_sm;
						font-weight: variables.$fontWeightMedium;
					}

					.content_desc {
						color: variables.$textColorLightGray;
						margin-top: 13px;
						@include mixins.font_size_sm;
					}
				}
			}
		}
	}

	.welcome_banner_control {
		position: absolute;
		height: 70px;
		background: rgba(#fff, .95);
		margin-left: calc((100% - #{variables.$defaultInnerWidth}) / 2 + 110px);
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 1;

		.banner_control_inner {
			@include mixins.justify_width(center);
			width: 1090px;
			height: 100%;
			//background: rgba(#0f0, .5);

			.banner_group_list {
				@include mixins.justify_width(center, around);
				flex: 0 0 893px;
				height: 100%;

				.banner_group_item {
					height: 100%;
					border: {
						top: 2px solid transparent;
						bottom: 2px solid transparent;
					}
					box-sizing: border-box;

					&.active {
						border-bottom: 2px solid #000;

						.btn_banner_group {
							color: variables.$textColorBlack;
							font-weight: variables.$fontWeightBold;
						}
					}

					.btn_banner_group {
						position: relative;
						color: variables.$textColorLightGray;
						font-size: variables.$fontSizeSm;
						line-height: 66px;
						letter-spacing: variables.$letterSpacing;
						font-weight: variables.$fontWeightMedium;

						&.live {
							padding-left: 11px;

							&:before {
								content: '';
								position: absolute;
								display: inline-block;
								width: 5px;
								height: 5px;
								border-radius: 50%;
								background: #ff393b;
								left: 0;
								top: 50%;
								transform: translateY(-50%);
							}
						}
					}
				}
			}

			.divider {
				flex: 0 0 1px;
				height: 16px;
				background: rgba(#000, .1);
			}

			.banner_btn_box {
				flex: 1;
				@include mixins.justify_width(center, between);
				margin-left: 30px;

				.swiper-button-prev, .swiper-button-next {
					position: relative;
					width: 30px;
					height: 30px;
					margin-top: 0;
					background-color: transparent;
					@include mixins.ratio_images('../../ink/welcome/images/arw_welcome_main_swiper', 'png', 30px, 30px);
					top: auto;
					left: auto;
					right: auto;
				}

				.swiper-button-next {
				}

				.btn_show_all_banner {
					width: 50px;
					height: 50px;
					@include mixins.ratio_images('../../ink/welcome/images/btn_welcome_show_all', 'png', 50px, 50px);
				}
			}
		}
	}

	.circle_progress_wrap {
		width: 30px;
		height: 30px;
	}
}

/* 웰컴 주력 마케팅 영역 */
.welcome_marketing_wrap {
	margin: 70px 0;

	.welcome_marketing_list {
		@include mixins.justify_width(center, between);

		.welcome_marketing_item {
			flex: 0 0 90px;
			text-align: center;

			a {
				display: block;
				line-height: 0;
			}

			.ico_welcome_marketing {
				display: inline-block;
				width: 68px;
				height: 68px;
			}

			@for $i from 1 through 12 {
				&:nth-child(#{$i}) .ico_welcome_marketing {
					background: url('../../ink/welcome/images/ico_welcome_marketing_' + $i + '.svg');
				}
			}

			.marketing_name {
				display: block;
				color: variables.$textColorGray;
				margin-top: 10px;
				@include mixins.font_size_xs;
			}
		}
	}
}

/* 웰컴 컨텐츠 블럭단위 영역 */
.welcome_main_content_area {
	~ .welcome_main_content_area {
		margin-top: 110px;
	}

	&.type_gray {
		padding: 100px 0 120px;
		background: variables.$colorGray06;
	}

	.btn_more_plus_text {
		@include mixins.font_size_sm;

		.ico_plus {
			width: 10px;
			height: 10px;
			margin: 6px 0 0 6px;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_plus_h10_bold', 'png', 10px, 10px);
		}

		&.type_white {
			color: #fff;

			.ico_plus {
				@include mixins.ratio_images('../images/ico_plus_h10_bold_white', 'png', 10px, 10px);
			}
		}
	}

	.outside_nav {
		.swiper-button-prev {
			left: -64px;
		}

		.swiper-button-next {
			right: -64px;
		}
	}

	// 상품형 스와이퍼
	.welcome_prod_swiper_wrap {
		.prod_item, .prod_thumb_box, .img_box {
			width: 211px;
			@include mixins.transition(width .25s);
		}

		.prod_area {
			.prod_thumb_box {
				.img_box {
					img {
						max-height: calc(211px * 1.618 - 10px);
					}
				}
			}
		}

		.swiper-wrapper.hover_zoom {
			&:hover, &:focus-within {
				.prod_item {
					&, .prod_thumb_box, .img_box {
						width: 195px;
					}

					&:hover, &:focus {
						&, .prod_thumb_box, .img_box {
							width: 276px;
						}

						.img_box img {
							max-height: calc(276px * 1.618 - 10px);
						}

						.prod_price {
							display: block;
						}
					}
				}
			}
		}

		.prod_price {
			display: none;
		}

		.prod_used_price {
			margin-top: 12px;

			.used_grade {
				display: inline-block;
				color: variables.$textColorLightGray;
				padding: 0 5px;
				border: 1px solid variables.$colorGray02;
				border-radius: 4px;
				font-size: variables.$fontSizeMin;
				line-height: 21px;

				&.highest {
					color: variables.$kyoboPurple;
					border-color: variables.$kyoboPurple;
				}

				&.high {
					color: variables.$kyoboGreen;
					border-color: variables.$kyoboGreen;
				}

				&.middle, &.lower, &.lowest {

				}
			}

			.price {
				@include mixins.font_size_xs;

				.val {
					font-weight: variables.$fontWeightBold;
				}
			}

			.used_discount {
				margin-top: 16px;
				padding: {
					top: 11px;
					left: 14px;
				}
				border-top: 1px solid variables.$colorGray04;
				background: {
					position: left top 17px;
					repeat: no-repeat;
				}
				@include mixins.ratio_images('../images/ico_checkbox_active_02', 'png', 10px, 7px);
				@include mixins.font_size_xxs;
			}
		}
	}

	.welcome_live_list_wrap {
		margin-top: 41px;

		.swiper-slide {

			.prod_item, .prod_thumb_box, .live_thumb_box {
				width: 211px;
			}
		}
	}

	.live_tag_wrap {
		@include mixins.justify_width(center, between);

		.form_sel {
			margin-left: 20px;
		}
	}

	// 오늘 추천
	.showcase_template_wrap {
		.template_row .template_col {
			max-width: inherit;
		}

		.expand_related_book_wrap {

			margin-left: calc((100% - #{variables.$defaultInnerWidth}) / 2);
			padding-right: calc((100% - #{variables.$defaultInnerWidth}) / 2 + #{variables.$defaultInnerWidth} - 680px);

			.detail_swiper_wrap {
				width: 720px;
				padding-right: 80px;

				.swiper-container {
					overflow: visible;
				}

				.swiper-slide-active {
					.prod_thumb_box {
						box-shadow: 10px 10px 30px rgba(#000, .08);

						&:before {
							content: none;
						}
					}
				}

				.prod_info_box {
					margin-left: 30px;
				}
			}

			.thumb_swiper_wrap {
				width: calc(100% - 720px + 162px);

				.thumb_list {
					align-items: flex-start;
				}

				.prod_area {
					.prod_info_box {
						margin-top: 12px;
					}
				}
			}

			.swiper-button-prev {
				left: -64px;
			}

			.swiper-button-next {
				right: calc(100% - #{variables.$defaultInnerWidth} - 64px);
			}
		}
	}

	// e : 오늘추천

	// 유연화 컨텐츠 영역
	.fluid_swiper_wrap {
		.prod_thumb_box {
			width: 100%;

			&.type_cover_circle {
				~ .prod_info_box {
					text-align: left;
				}
			}
		}

		.swiper-container:not(.swiper-container-horizontal) {
			.swiper-slide {
				margin-right: 36px;
			}

			&[data-col='4'] {
				.swiper-slide {
					flex: 1 0 calc((100% - 108px) / 4);
				}
			}

			&[data-col='3'] {
				.swiper-slide {
					flex: 1 0 calc((100% - 72px) / 3);
				}
			}

			&[data-col='5'] {
				.swiper-slide {
					flex: 1 0 calc((100% - 144px) / 5);
				}
			}
		}
	}

	// e : 핫 아이템

	// Picks
	.picks_wrap {
		background: rgba(variables.$kyoboGreen, .06);

		.picks_logo {
			display: inline-block;
			width: 82px;
			height: 45px;
			background: url('../../ink/welcome/images/ico_welcome_picks.svg') center no-repeat;
			vertical-align: top;
		}

		.picks_wrap_inner {
			padding: 80px 0 84px;

			//.title_wrap.title_size_xxl .title_heading {
			//	height: 45px;
			//}

			.picks_contents {
				@include mixins.justify_width(end, between);

				.left_area {
					@include mixins.justify_width(start, between, column);
					width: 302px;
					height: 304px;

					.picks_info_box {
						flex: 1;

						.picks_desc {
							color: variables.$textColorGreen;
							margin-bottom: 7px;
							@include mixins.font_size_sm;
							font-weight: variables.$fontWeightMedium;
						}

						.picks_info {
							font-size: 24px;
							line-height: 35px;
							letter-spacing: variables.$letterSpacing;
							font-weight: variables.$fontWeightBold;

							&.user_type {
								position: relative;
								color: variables.$textColorGreen;

								.user_level {
									position: absolute;
									display: inline-block;
									color: #fff;
									height: 22px;
									padding: 0 8px;
									background: rgba(variables.$kyoboGreen, .9);
									border-radius: 11px;
									font-size: variables.$fontSizeMin;
									line-height: variables.$fontSizeXsLineHeight;
									letter-spacing: variables.$letterSpacing;
									top: -3px;
									right: -29px;
									box-shadow: 0 5px 10px rgba(variables.$kyoboGreen, .4);
								}
							}
						}

						.picks_data_list {
							margin-top: 25px;

							.picks_data_item {
								.ico_author, .ico_book, .value {
									display: inline-block;
									@include mixins.font_size_xs;
								}

								.ico_author, .ico_book {
									color: variables.$textColorGray;
									padding-left: 23px;
									background: {
										position: left center;
										repeat: no-repeat;
									}
								}

								.ico_author {
									@include mixins.ratio_images('../../ink/welcome/images/ico_welcome_picks_author', 'png', 18px, 18px);
								}

								.ico_book {
									@include mixins.ratio_images('../../ink/welcome/images/ico_welcome_picks_book', 'png', 18px, 18px);
								}

								.value {
									margin-left: 6px;
								}

								+ .picks_data_item {
									margin-top: 12px;
								}
							}
						}
					}

					.tag_wrap {
						width: 100%;
					}
				}

				.right_area {
					flex: 1;
					margin-left: 38px;

					.picks_item_list {
						@include mixins.justify_width(center, between);

						.picks_item, .login_item {
							position: relative;
							width: 200px;
							height: 333px;
						}

						.picks_item {

							.picks_item_inner {
								position: relative;
								width: 100%;
								height: 333px;
								top: 0;
								@include mixins.transition(height .25s, top .25s);
							}

							.front, .back {
								width: 100%;
								height: 100%;
								background: #fff;
								border-radius: 16px;
								overflow: hidden;
							}

							.picks_info_title {
								@include mixins.font_size_xs;
								font-weight: variables.$fontWeightBold;
							}

							.front {
								.img_box {
									height: 200px;
								}

								.picks_info {
									height: calc(100% - 200px);
									border: 1px solid variables.$colorGray04;
									border-top: none;
									border-radius: 0 0 16px 16px;
									padding-top: 20px;
									box-sizing: border-box;
									text-align: center;

									.picks_info_desc {
										color: variables.$textColorGray;
										margin-top: 6px;
										@include mixins.font_size_xxs;
									}
								}
							}

							.back {
								display: none;
								text-align: center;

								.picks_blur {
									position: absolute;
									width: 100%;
									height: 100%;
									left: 0;
									top: 0;
									border-radius: 16px;

									.blur_img_box {
										height: 100%;

										&:before {
											content: "";
											position: absolute;
											top: 0;
											left: 0;
											width: 100%;
											height: 100%;
											background-color: rgba(0, 0, 0, 0.6);
											z-index: 2;
										}

										img {
											display: block;
											width: 100%;
											//top: 50%;
											//left: 0;
											//transform: translateY(-50%);
										}
									}
								}

								.picks_info_title {
									position: relative;
									color: #fff;
									margin-top: 20px;
									z-index: 2;
									text-align: center;
								}

								.picks_back_contents {
									padding: 30px 20px 40px;

									.picks_img_link {
										display: block;

										.img_wrap {
											position: relative;
											padding: 0 21px;
											//height: 175px;
											//overflow: hidden;

											img {
												position: relative;
												width: 100%;
												max-height: calc(118px * 1.618 - 10px);
												//top: 50%;
												//transform: translateY(-50%);
