					.review_summary_wrap {
						margin-top: 30px;

						.review_quotes_text {
							margin-top: 0;
						}
					}

					.prod_introduction {
						margin-top: 20px;
						@include mixins.multi_ellipsis(variables.$fontSizeXsLineHeight);
					}

					.prod_showing {
						margin-top: 30px;
					}

					.bubble_speech_desc {
						position: relative;
						display: inline-block;
						margin-top: 26px;
						box-shadow: none;
					}

					.btn_wrap {
						margin-top: 30px;
						text-align: left;

						.btn_md {
							font-weight: variables.$fontWeightRegular;

							&.btn_wish {
								width: 44px;
								min-width: 44px;
								padding: 0;

								.ico_wish {
									margin: 0;
								}
							}
						}

					}
				}

			}
		}
	}
}

/* 서비스 그룹 */
.welcome_main_service_area {
	margin-top: 60px;
	padding: 40px 0 50px;
	background: variables.$colorGray06;
	border-top: 1px solid variables.$colorGray04;

	.service_group_area {
		@include mixins.justify_width(start);
		align-items: stretch;

		.service_group_box {
			flex: 1;

			+ .service_group_box {
				border-left: 1px solid variables.$colorGray04;
				padding-left: 37px;
			}

			.group_name {
				display: inline-block;
				@include mixins.font_size_xs;
				font-weight: variables.$fontWeightBold;
			}

			a.group_name {
				padding-right: 22px;
				background: {
					repeat: no-repeat;
					position: right center;
				}
				@include mixins.ratio_images('../images/arw_anb_category_all', 'png', 16px, 16px);
			}

			.service_list {
				margin-top: 16px;

				.service_item {
					color: variables.$textColorGray;
					@include mixins.font_size_xxs;

					+ .service_item {
						margin-top: 10px;
					}
				}
			}
		}
	}
}

.dialog_wrapper .ui-dialog.dialog_all_banner {
	width: 1288px;

	.btn_dialog_close {
		top: 0;

		.ico_dialog_close {
			@include mixins.ratio_images('../images/btn_dialog_close_w', 'png', 24px, 24px);
		}
	}

	.dialog_wrap {
		padding: 0;

		&, .dialog_contents {
			background: transparent;
		}

		.dialog_contents {
			margin-top: 50px;
		}
	}
}

.all_banner_list {
	@include mixins.justify_width;
	flex-wrap: wrap;
	font-size: 0;

	.all_banner_item {
		flex: 0 0 625px;
		border-radius: 30px;
		overflow: hidden;

		&:nth-child(2n) {
			margin-left: 20px;
		}

		&:nth-child(n + 3) {
			margin-top: 20px;
		}
	}
}


.circle_progress_wrap {
	position: relative;

	.circle_progress {
		transform: rotate(-90deg);
	}
	.bar {
		fill: none;
		stroke: #000;
		stroke-linecap: round;
	}
}